
<template>
    <div >
        <h2>基金详情--{{params.name}} 代码:{{params.code}}</h2>
        <h3>基金策略</h3>
        <truncate class='strategy' clamp="Show more" :length="200" :text="strategy" :clamp="'点击查看更多'" :less="'点击收起'"/>
        <br>
        <h3>基金管理人</h3>
        <div class="link-text">
            <span v-for="manager in managers" :key="manager.code" @click="gotoDetail(manager.code,manager.name)">{{manager.name}}</span>
        </div>
        <br>
        <h3>基金持仓</h3>
        <div v-for="(quarter,index) in showedHoldings" :key="index" >
            <h4>{{quarter.year}} 年 {{quarter.quarter}} 季度</h4>
            <table-plus :data="quarter.holdings">
                <el-table-column  prop="stockCode" label="股票代码" width="100" />
                <el-table-column  prop="stockName" label="股票名称" width="100" />
                <el-table-column  prop="ratioinN" :formatter="percentFormatter" label="占净值比例"width="100" />
                <el-table-column  prop="ratioinS" :formatter="percentFormatter" label="占权益比" width="100" />
                <el-table-column  prop="ratioinO" :formatter="percentFormatter2" label="占股票流通比例" width="130" />
            </table-plus>

        </div>
        <el-button type="primary" @click="switchShow">{{!showHoldingHistory?'显示历史持仓':'隐藏历史持仓'}}</el-button>
    </div>
</template>
<script>
  import { get,post }  from '../../common/function/service';
  import truncate from 'vue-truncate-collapsed';

  export default {
      data() {
		  return {
			params: '',
            strategy:'',
            managers:[],
            holdings:[],
            showHoldingHistory: false
		  }
      },
      created() {
        this.params = this.$route.params;
        let code = this.params.code;
        get(`/finance/fund/${code}/strategy`).then(res=>{
          if(res.code === 0)this.strategy = res.data;
        });
        get(`/finance/fund/${code}/manager`).then(res=>{
          if(res.code === 0)this.managers = res.data;
        });
        get(`/finance/fund/${code}/holding`).then(res=>{
          let holding = {}
          let func = h => {
            let key = h.year * 10 + h.quarter;
            if (!holding[key]) {
              holding[key] = {};
            }
            if (!holding[key][h.stockCode]) {
              holding[key][h.stockCode] = h;
            }
          };
          res.data.big10.forEach(func);
          res.data.detail.forEach(func);
          this.holdings = [];
          for(let key of Object.keys(holding)){
            let intKey = parseInt(key)
            let quarter = intKey % 10;
            let year = Math.floor(intKey /10);
            let arr  = []
            for(let key2 of Object.keys(holding[key])){
              arr.push(holding[key][key2])
            }
            this.holdings.push({quarter,year,intKey,holdings:arr})
          }
          this.holdings.sort((a,b)=>b.intKey-a.intKey)
        });
      },
      computed: {
        showedHoldings(){
          return this.showHoldingHistory? this.holdings:this.holdings[0]?[this.holdings[0]]:[]
        }
      },

      methods: {
        gotoDetail: function(code,name){
          this.$router.push({path: `/manager/${code}/${name}`})
        },
        percentFormatter(row, column,cellValue) {
          if(cellValue==null)return 'NA';
          return cellValue.toFixed(2) + '%';
        },
        percentFormatter2(row, column,cellValue) {
          if(cellValue==null)return 'NA';
          return cellValue + '%';
        },
        switchShow(){
          this.showHoldingHistory = !this.showHoldingHistory;
        }
      },
      components: {
        truncate:truncate
      },
    }
</script>
<style lang="scss" scoped>

    .link-text{
        text-decoration: underline;
        &:hover{
            cursor: pointer;
        }
    }

</style>

